export class ExtentionApi extends CrudApi {
  // baseURL = `https://buzzguru.com/api`;
  baseURL = 'https://api.buzz.guru/api/external/extension';

  findChannel(params) {
    // const data = await this.fetch(`${this.baseURL}/channel`, {
    //   method: 'POST',
    //   data: params,
    // });
    // TODO: 
    return new Promise((res, rej) => {
      window.__lskjs.GM_xmlhttpRequest({
        url: `${this.baseURL}/channel`,
        method: 'POST',
        headers: {
          'content-type': 'application/json',
        },
        data: JSON.stringify(params),
        onload(response) {
          const dataResponse = JSON.parse(response.responseText);
          const data = dataResponse?.data;
          res({
            data,
            status: dataResponse.status,
          });
        },
        onerror: rej,
      });
    });
  }
}

class ExtentionStore extends CrudStore {
  static Api = ExtentionApi;

  static async findChannel(params) {
    return this.wrap(this.api.findChannel(params));
  }
  @observable _id;
  @observable info;
  @observable channelInfo;
  @observable type = 'youtube';

  @observable.deep viewsDynamics;
  @observable.deep viewsDynamics2;
  @observable.deep subscribersDynamics;
  @observable.deep subscribersDynamics2;
  @observable.deep dynamics;
  @observable.deep streamsDynamics;
  @observable.deep ratings;
  @observable.deep productMentions;
  @observable.deep gamesRatings;
  @observable.deep publicationTime;
  @observable.deep forecastViews;
  @observable.deep metrics;
  @observable.deep streamsMetrics;
  @observable.deep channelRelations;
}

export default ExtentionStore;
