import get from 'lodash/get';

export default (item, type = 'youtube') => {
  if (!item) return [];
  if (type === 'twitch') {
    return [
        {
          USERNAME: get(item, 'name', ''),
        },
        {
          LINK: document?.location?.href || '',
        },
        {
          TRACKER: get(item, 'tracker', ''),
        },
        {
          COUNTRY: get(item, 'country', []),
        },
        {
          LANGUAGE: get(item, 'language', []),
        },
        {
          FOLLOWERS: get(item, 'subscribers', 0),
        },
        {
          CCV: get(item, 'ccv', 0),
        },
        {
          CONTENT: get(item, 'content', 0),
        },
        {
          EMAIL: get(item, 'email', '0'), //not email from api
        },
        {
          OTHER_CONTACT: get(item, 'contacts', []).join(', '), //not contacts from api
        },
      ];
  }
  if (type === 'tiktok') {
    return [
        {
          USERNAME: get(item, 'name', ''),
        },
        {
          LINK: document?.location?.href || '',
        },
        {
          COUNTRY: get(item, 'country', []),
        },
        {
          FOLLOWERS: get(item, 'subscribers', 0),
        },
        {
          VIEWS: get(item, 'views', 0),
        },
        {
          CONTENT: get(item, 'content', 0),
        },
        {
          EMAIL: get(item, 'email', '0'), //not email from api
        },
        {
          OTHER_CONTACT: get(item, 'contacts', []).join(', '), //not contacts from api
        },
        {
          OTHER_LINKS: get(item, 'links', []).join(', '), //not links from api
        },
        {
          ER: get(item, 'er', 0),
        },
        {
          REACH: get(item, 'reach', 0),
        },
      ];
  }
  if (type === 'instagram') {
    return [
        {
          USERNAME: get(item, 'name', ''),
        },
        {
          LINK: document?.location?.href || '',
        },
        {
          COUNTRY: get(item, 'country', []),
        },
        {
          FOLLOWERS: get(item, 'subscribers', 0),
        },
        {
          LIKES: get(item, 'likes', 0),
        },
        {
          VIEWS: get(item, 'views', 0),
        },
        {
          CONTENT: get(item, 'content', 0),
        },
        {
          EMAIL: get(item, 'email', '0'), //not email from api
        },
        {
          OTHER_CONTACT: get(item, 'contacts', []).join(', '), //not contacts from api
        },
        {
          OTHER_LINKS: get(item, 'links', []).join(', '), //not links from api
        },
        {
          ER: get(item, 'er', 0),
        },
        {
          REACH: get(item, 'likes', 0) * 2,
        },
      ];
  }
  // default youtube
  return [
    {
      USERNAME: get(item, 'name', ''),
    },
    {
      LINK: document?.location?.href || '',
    },
    {
      EMAIL: get(item, 'email', '0'), //not email from api
    },
    {
      OTHER_CONTACT: get(item, 'contacts', []).join(', '), //not contacts from api
    },
    {
      COUNTRY: get(item, 'country', ''),
    },
    {
      LANGUAGE: get(item, 'language', ''),
    },
    {
      CONTENT: get(item, 'content', 0),
    },
    {
      SUBSCRIBERS: get(item, 'subscribers', 0),
    },
    {
      AV_VIEWS: get(item, 'views', 0),
    },
  ];
};
